import { action, makeObservable } from "mobx";
import { Location, NavigateFunction } from "react-router-dom";
import AccountsManager from "../../core/Accounts";

class LoginPageModel {
  constructor(readonly navigate: NavigateFunction, readonly location: Location) {
    makeObservable(this, {
      login: action,
    });
  }

  async login({ email = "", password = "" }) {
    await AccountsManager.shared.login(email, password);

    // @ts-ignore
    const from = this.location.state?.from?.pathname || "/";
    this.navigate(from, { replace: true });
  }
}

export default LoginPageModel;
