import React, { FC } from "react";
import { observer } from "mobx-react-lite";
import { useLocation, matchPath, Outlet, Navigate } from "react-router-dom";

import AccountsManager from "../../core/Accounts";
import * as S from "./styled";
import Popup from "reactjs-popup";

const routes: Record<string, any> = {
  "/account": { title: "Account" },
  "/income": { title: "Income" },
  "/metrics": { title: "Metrics" },
  "/profile": { title: "Profile" },
  "/integrations": { title: "Integrations" },
  "/payments": { title: "Payments" },
};

const Layout = () => {
  const location = useLocation();
  const account = AccountsManager.shared.account;

  if (account == null) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  return (
    <S.Layout>
      <S.Header>
        <h2>{routes[location.pathname]?.title}</h2>

        <S.CompanyBadge>
          <Popup position="bottom right" trigger={<p>{account.profile.companyName}</p>} offsetY={8}>
            <S.Tooltip onClick={() => AccountsManager.shared.logout()}>Logout</S.Tooltip>
          </Popup>
        </S.CompanyBadge>
      </S.Header>

      <S.Sidebar>
        <S.SidebarLogo>
          <img src="assets/logo.svg" alt="logo" />
        </S.SidebarLogo>

        <S.SidebarSection>Dashboard</S.SidebarSection>
        <SidebarItem to="/account">Account</SidebarItem>
        <SidebarItem to="/income">Income</SidebarItem>
        <SidebarItem to="/metrics">Metrics</SidebarItem>

        <S.SidebarSection>Settings</S.SidebarSection>
        <SidebarItem to="/payments">Payments</SidebarItem>
        <SidebarItem to="/integrations">Integrations</SidebarItem>
        <SidebarItem to="/profile">Profile</SidebarItem>
      </S.Sidebar>

      <S.Page>
        <Outlet />
      </S.Page>
    </S.Layout>
  );
};

const SidebarItem: FC<{ to: string; children: string }> = ({ to, children }) => {
  const location = useLocation();

  return (
    <S.SidebarItem to={to} $selected={matchPath(to, location.pathname) != null}>
      {children}
    </S.SidebarItem>
  );
};

export default observer(Layout);
