import { action, makeObservable } from "mobx";
import { Location, NavigateFunction } from "react-router-dom";
import { RegistrationUserSchema } from "../../generated";
import AccountsManager from "../../core/Accounts";

export type SignupForm = RegistrationUserSchema;

class SignupPageModel {
  constructor(readonly navigate: NavigateFunction, readonly location: Location) {
    makeObservable(this, {
      signup: action,
    });
  }

  signup = async (data: SignupForm) => {
    await AccountsManager.shared.signup(data);
    this.navigate("/", { replace: true });
  };
}

export default SignupPageModel;
