import { observer } from "mobx-react-lite";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { Button } from "../../uikit/Button";
import { Input } from "../../uikit/Input";
import SignupPageModel, { SignupForm } from "./signup.model";
import * as S from "./styled";

const RegisterPage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { register, handleSubmit } = useForm<SignupForm>();
  const [model] = useState(() => new SignupPageModel(navigate, location));

  return (
    <S.Content>
      <S.CardForm onSubmit={handleSubmit((data) => model.signup(data))}>
        <img src="assets/logo.svg" alt="logo" />
        <S.NoAccount>
          Do you already have an account? <Link to="/login">Login</Link>
        </S.NoAccount>
        <br />

        <Input {...register("user_name")} placeholder="Firstname Lastname" />
        <Input {...register("company_name")} placeholder="Company name" />
        <Input {...register("company_country_of_residence")} placeholder="Company country" />

        <br />

        <Input {...register("email")} placeholder="Email" type="email" />
        <Input {...register("password")} placeholder="Password" type="password" />

        <S.Buttons>
          <Button type="submit">Signup</Button>
        </S.Buttons>
      </S.CardForm>
    </S.Content>
  );
};

export default observer(RegisterPage);
