import { action, makeObservable, observable } from "mobx";
import { UsersService } from "../generated";

class Account {
  public profile = {
    email: "",
    username: "",
    companyName: "",
    companyCountry: "",
  };

  constructor(private _accessToken: string, private refreshToken: string) {
    makeObservable(this, {
      profile: observable,
      changeProfile: action,
    });
  }

  changeProfile(profile: Partial<Account["profile"]>) {
    this.profile = { ...this.profile, ...profile };
  }

  async changePassword(pwd: string, newPwd: string) {
    await UsersService.settingsApiV1UsersSettingsPatch({
      current_password: pwd,
      new_password: newPwd,
      email: this.profile.email,
    });
  }

  async changeEmail(pwd: string, email: string) {
    await UsersService.settingsApiV1UsersSettingsPatch({
      current_password: pwd,
      email: email,
    });

    this.changeProfile({ email });
  }

  async fetchProfile() {
    const profile = await UsersService.getProfileApiV1UsersProfileGet();
    this.changeProfile({
      companyCountry: profile.info.company_country_of_residence,
      companyName: profile.info.company_name,
      username: profile.user_name,
      email: profile.email,
    });
  }

  async getAccessToken() {
    console.log("getAccessToken", this._accessToken);
    // todo: refresh
    return this._accessToken;
  }
}

export default Account;
